// Function to fetch the data for a single, specific Surah
async function getSurahDetail(id) {
  const res = await fetch(`http://localhost:3000/api/surahs/${id}`);
  if (!res.ok) {
    throw new Error('Failed to fetch surah details');
  }
  return res.json();
}

// This is the main page component. It receives "params" which contains the id from the URL.
export default async function SurahPage({ params }) {
  // Fetch the data for the requested Surah
  const { surah, ayahs } = await getSurahDetail(params.id);

  return (
    <div className="bg-gray-100 min-h-screen">
      <header className="bg-green-700 text-white p-6 shadow-md text-center">
        <h1 className="text-4xl font-bold font-['Amiri',_serif]">{surah.name_arabic}</h1>
        <p className="text-2xl font-semibold">{surah.name_english}</p>
        <p className="text-lg">{surah.revelation_type}</p>
      </header>
      
      <main className="p-4 md:p-8 max-w-4xl mx-auto">
        {/* We map over the ayahs and display each one in its own card */}
        {ayahs.map((ayah) => (
          <div key={ayah.ayah_number} className="bg-white p-6 mb-4 rounded-lg shadow-sm">
            <p className="text-right text-3xl font-['Amiri',_serif] text-gray-800 leading-relaxed mb-4">
              {ayah.text_uthmani}
            </p>
            <p className="text-gray-600">
              {ayah.ayah_number}. {ayah.translation_text}
            </p>
          </div>
        ))}
      </main>
    </div>
  );
}